import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import "react-tabs/style/react-tabs.css";

import ScribbleButton from './ScribbleButton';

import WebTimeline from './WebTimeline';
import GraphicTimeline from './GraphicTimeline';
import VideoTimeline from './VideoTimeline';
 
export default class WorkTabs extends React.Component {
 
  render() { 
    return (
      <Tabs>
        <TabList>
          <Tab><ScribbleButton text="Web"/></Tab>
          <Tab><ScribbleButton text="Graphic"/></Tab>
          <Tab><ScribbleButton text="Video"/></Tab>
        </TabList>
    
        <TabPanel>
          <WebTimeline/>
        </TabPanel>

        <TabPanel>
          <GraphicTimeline/>
        </TabPanel>
        
        <TabPanel>
          <VideoTimeline/>          
        </TabPanel>
      </Tabs>
    );
  }
}