import React from 'react';
import { Link, NavLink  } from 'react-router-dom';

		
var kute = require("kute.js"); //grab the core
require("kute.js/kute-svg"); // Add SVG Plugin
require("kute.js/kute-css"); // Add CSS Plugin
require("kute.js/kute-attr"); // Add Attributes Plugin
require("kute.js/kute-text");

kute.defaultOptions.duration = 300;

const style = {
	wrapper: {
		width: '100%',
		height: '100%',
		position: 'fixed',
		top: 0,
		zIndex: 10,
		pointerEvents: 'none',
	},
	container: {
		width: 70,
		height: 70,
		position: 'absolute',
		right: 10,
		top: 10,
		cursor: 'pointer',
		pointerEvents: 'all'
	},
	img: {
		width: '100%',
		height: '100%',
		transition: 'transform .6s ease',
		pointerEvents: 'none',
		position: 'absolute',
		left: 0
	},
	svg: {
		width: '55%',
		height: '60%',
		position: 'absolute',
		left: '33.5%',
		top: '36.5%',
	},
	ul: {
		color: 'white',
		listStyle: 'none',
		fontWeight: 'bold',
		position: 'absolute',
		margin: 0,
		padding: 0,
		left: '50%',
		top: 50,
		transform: 'translateX(-50%)',
		fontSize: '200%',
		pointerEvents: 'inherit',
		lineHeight: '10vh',
	}
}

export default class Navigation extends React.Component {
	state = {
		open: false,

	}
	
	toggle = () => {
		if (this.state.open == false) {
			var multiMorph1 = kute.to('#menu1', { path: '#x1' }).start();
			var multiMorph2 = kute.to('#menu2', { path: '#x3' }).start();
			var multiMorph3 = kute.to('#menu3', { path: '#x2' }).start();
			this.setState({open: true});
		} else {
			var multiMorph4 = kute.to('#menu1', { path: '#burger1' }).start();
			var multiMorph5 = kute.to('#menu2', { path: '#burger2' }).start();
			var multiMorph6 = kute.to('#menu3', { path: '#burger3' }).start();
			this.setState({open: false});
		}
	}

	render() {
		return (
			<div style={style.wrapper}>

				<div style={style.container} onClick={this.toggle}>
					<img style={{...style.img, transform: `scale(${this.state.open ? '150' : '1'})`}} src="./assets/img/menu_button.png" alt="menu"/>

					<svg style={style.svg} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100">
						<path id='menu1' fill="#fff" d="M6.77,3C23,4.4,39.44,3.59,55.68,3.59a1.5,1.5,0,0,0,0-3L6.77,0c-1.93,0-1.93,3,0,3l48.91.58v-3C39.44.59,23,1.4,6.77,0,4.85-.16,4.86,2.84,6.77,3Z" transform="translate(-1.26)"/><path id='menu2' fill="#fff" d="M15,32.63,56,30.1c1.91-.12,2-3,0-3H4.85a1.51,1.51,0,0,0-.4,2.95L14.6,32.58c1.87.46,2.67-2.43.79-2.9L5.25,27.15l-.4,3H56v-3L15,29.63C13.08,29.75,13.06,32.75,15,32.63Z" transform="translate(-1.26)"/><path id='menu3' fill="#fff" d="M2.73,61.65C6.09,62,10,61,13.33,60.59L28.22,58.9,57,55.61a1.5,1.5,0,0,0,0-3A195.22,195.22,0,0,0,2.33,58.7c-1.87.48-1.08,3.38.8,2.9a192.16,192.16,0,0,1,53.87-6v-3L28.22,55.9l-13.9,1.58c-3.65.42-7.95,1.58-11.59,1.17C.81,58.44.83,61.44,2.73,61.65Z" transform="translate(-1.26)"/>
						<path style={{visibility:'hidden'}} id='burger1' fill="#fff" d="M6.77,3C23,4.4,39.44,3.59,55.68,3.59a1.5,1.5,0,0,0,0-3L6.77,0c-1.93,0-1.93,3,0,3l48.91.58v-3C39.44.59,23,1.4,6.77,0,4.85-.16,4.86,2.84,6.77,3Z" transform="translate(-1.26)"/><path style={{visibility:'hidden'}} id='burger2' fill="#fff" d="M15,32.63,56,30.1c1.91-.12,2-3,0-3H4.85a1.51,1.51,0,0,0-.4,2.95L14.6,32.58c1.87.46,2.67-2.43.79-2.9L5.25,27.15l-.4,3H56v-3L15,29.63C13.08,29.75,13.06,32.75,15,32.63Z" transform="translate(-1.26)"/><path style={{visibility:'hidden'}} id='burger3' fill="#fff" d="M2.73,61.65C6.09,62,10,61,13.33,60.59L28.22,58.9,57,55.61a1.5,1.5,0,0,0,0-3A195.22,195.22,0,0,0,2.33,58.7c-1.87.48-1.08,3.38.8,2.9a192.16,192.16,0,0,1,53.87-6v-3L28.22,55.9l-13.9,1.58c-3.65.42-7.95,1.58-11.59,1.17C.81,58.44.83,61.44,2.73,61.65Z" transform="translate(-1.26)"/>
						<path style={{visibility:'hidden'}} id='x1' fill="#fff" d="M14.14 70.85L85.05 19.7c1.56-1.12.06-3.72-1.51-2.59L12.62 68.26c-1.55 1.12 0 3.72 1.52 2.59z"/><path style={{visibility:'hidden'}} id='x2' fill="#fff" d="M20.38 14.32c10.73 8.75 21.23 17.75 30.72 27.86 9.16 9.75 17.5 20.22 25.83 30.67 1.21 1.51 3.32-.63 2.13-2.12-8.34-10.45-16.69-20.92-25.83-30.67C43.74 29.94 33.23 21 22.5 12.2c-1.48-1.21-3.62.9-2.12 2.12z"/><path style={{visibility:'hidden'}} id='x3' fill="#fff" d="M1.48 4.31c-2 12.12-.67 24.22-.11 36.4a276.62 276.62 0 0 1-1 38.62 1.51 1.51 0 0 0 1.1 1.45c8.22 2.59 16.71 3.14 25.26 3.7 9.76.65 19.47 2 29.23 2.44a121.13 121.13 0 0 0 26.21-1.74c3.11-.52 9.53-.42 12-2.45 2.14-1.78 2.72-6.2 3.47-8.73 5.26-17.69 2.88-34.53.22-52.43l-2.09-14c-.39-2.65 0-5.76-2.7-6.89-3-1.27-8.42-.26-11.61-.27-5.18 0-10.35 0-15.53.06-11 .1-22 .31-33 .51-10.54.16-21.59-.59-31.86 2.08A1.5 1.5 0 0 0 1.86 6C10.35 3.75 19.33 4.2 28 4c8.43-.15 16.85-.32 25.27-.44s16.82-.21 25.23-.22c3.85 0 7.79-.2 11.64 0 2.67.18 2.2 1.6 2.55 4l1.12 7.49c2.47 16.63 6.36 33.7 2.89 50.45a73.72 73.72 0 0 1-3.09 11.32c-1 2.52-1.45 3.4-3.93 4.2a48.09 48.09 0 0 1-6.53 1.2 122 122 0 0 1-25.29 2c-9.2-.34-18.35-1.58-27.53-2.27-9.46-.71-18.94-1-28.06-3.85l1.1 1.45a277.4 277.4 0 0 0 1.08-36.69C4 30.12 2.28 17.58 4.38 5.11c.32-1.89-2.57-2.7-2.9-.8z"/>	
					</svg>
				</div>

				<nav style={{transition: 'opacity .3s ease', opacity: `${this.state.open ? '1' : '0'}`, pointerEvents: `${this.state.open ? 'all' : 'none'}`}}>
					<ul style={{...style.ul, pointerEvents: `${this.state.open ? 'all' : 'none'}`}}>
						<li><Link onClick={this.toggle} to='/'>Home</Link></li>
						<li><Link onClick={this.toggle} to='/work'>Work</Link></li>
						<li><Link onClick={this.toggle} to='/cv'>CV</Link></li>
						<li><Link onClick={this.toggle} to='/contact'>Contact</Link></li>
					</ul>
				</nav>

			</div>
		)
	}
}