import React from 'react';
import { Link } from 'react-router-dom';
const style = {
	container: {
		width: 100,
		height: 100,
		position: 'fixed',
		left: -15,
		top: -15,
		zIndex:10,
	},
	img: {
		width: '100%',
		height: '100%',
	},
}

export default class Logo extends React.Component {
	render() {
		return (
			<div style={style.container}>
				<Link onClick={this.toggle} to='/'><img style={style.img} src="./assets/img/logo.png" alt="logo"/></Link>
			</div>
		)
	}
}