import React from 'react';
import { Link } from 'react-router-dom';
const style = {
	container: {
		width: '100%',
		position: 'fixed',
		left: 0,
		bottom: 0,
		overflow: 'hidden',
		maxHeight: 150
	},
	img: {
		width: '140%',
		height: '100%',
		marginLeft: '-20%',
		marginBottom: -35,
		maxHeight: 150
	},
}

export default class Footer extends React.Component {
	render() {
		return (
			<footer>
				<div style={style.container}>
					<img style={style.img} src="./assets/img/footer_full.png" alt="footer"/>
				</div>
			</footer>
		)
	}
}