import React from 'react';

const style = {
  wrapper: {
    maxWidth: 1170, 
    margin: '0 auto'
  },
  img: {
    width: '95%',
    border: '1px solid black'
  }
}
 
export default class SvgCv extends React.Component {
  render() { 
    return (
      <div style={style.wrapper}>
        <img style={style.img} src="./assets/svg/cv1.svg" alt="cv"/>
        <img style={style.img} src="./assets/svg/cv2.svg" alt="cv"/>
        <img style={style.img} src="./assets/svg/cv3.svg" alt="cv"/>
      </div>
    );
  }
}